import * as React from 'react'
import AccountIcon from '../AccountIcon/AccountIcon'
import './SocialBar.css'

const SocialBar = ({ accounts }) => {
  return (
    <ul className='home-social'>
      {accounts.map((account) => (
        <li key={account.id}>
          <AccountIcon {...account} />
        </li>
      ))}
    </ul>
  )
}

export default SocialBar
