import * as React from 'react'

const CustomVoidLink = ({ href, className, title, onClick, children }) => {
  const customOnClick = (e) => {
    e.preventDefault()
    onClick()
  }
  return (
    <a href={href} className={className} title={title} onClick={customOnClick}>
      {children}
    </a>
  )
}

export default CustomVoidLink
