import * as React from 'react'
// CSS
import '../../css/index.css'

// COMPONENTS

import Header from '../Header/Header'
import SidebarMenu from '../SidebarMenu/SidebarMenu'
import SocialBar from '../SocialBar/SocialBar'
import Contact from '../Contact/Contact'
import Footer from '../Footer/Footer'
import { Helmet } from 'react-helmet'
import { accounts } from '../../dataSources/accounts'

// DATA
import { defaultMenu } from '../../dataSources/menu'

const UI = ({ title, children, menu = defaultMenu }) => {
  return (
    <div style={{ overflowX: 'hidden' }}>
      <Helmet>
        <meta charSet='utf-8' />
        <meta name='author' content='Nicolas Caballero'></meta>
        <meta name='description' content='Portfolio site for software development'></meta>
        <title>{title}</title>
        <link rel='canonical' href='https://n-bred.github.io/' />
      </Helmet>
      <Header>
        <SidebarMenu menuOptions={menu} />
      </Header>
      <SocialBar accounts={accounts} />
      {children}
      <Contact accounts={accounts} />
      <Footer />
    </div>
  )
}

export default UI
