import * as React from 'react'
import { Link } from 'gatsby'
import './SidebarMenu.css'
import scrollTo from '../../scripts/scrollTo'
import CustomVoidLink from '../CustomVoidLink/CustomVoidLink'

const SidebarMenu = ({ menuOptions }) => {
  const handleSideBar = () => {
    document.body.classList.toggle('menu-is-open')
  }
  return (
    <>
      <nav className='header-nav'>
        <CustomVoidLink href='#!' className='header-nav__close' title='close' onClick={handleSideBar}>
          <span>Close</span>
        </CustomVoidLink>

        <div className='header-nav__content'>
          <h3>Menu</h3>

          <ul className='header-nav__list'>
            {menuOptions.map((menuOption) => (
              <li className='current' key={menuOption.id}>
                {menuOption.pageLink ? (
                  <Link to={menuOption.to} className='text-white' title={menuOption.name}>
                    {menuOption.name}
                  </Link>
                ) : (
                  <CustomVoidLink
                    href='#!'
                    id={`menu-option-${menuOption.name}`}
                    className='text-white'
                    title={menuOption.name}
                    onClick={() => scrollTo(menuOption.to)}
                  >
                    {menuOption.name}
                  </CustomVoidLink>
                )}
              </li>
            ))}
          </ul>
        </div>
      </nav>

      <CustomVoidLink className='header-menu-toggle black-overlay' href='#!' onClick={handleSideBar}>
        <span className='header-menu-text black-overlay'>Menu</span>
        <span className='header-menu-icon' />
      </CustomVoidLink>
    </>
  )
}

export default SidebarMenu
